export interface ModifierGroup {
  /**
   * Modifier group ID.
   * @format GUID
   * @readonly
   */
  id?: string | null;
  /**
   * Revision number, which increments by 1 each time the modifier group is updated. To prevent conflicting changes, the current revision must be passed when updating the modifier group. Ignored when creating a modifier group.
   * @readonly
   */
  revision?: string | null;
  /**
   * Date and time the modifier group was created.
   * @readonly
   */
  createdDate?: Date | null;
  /**
   * Date and time the modifier group was updated.
   * @readonly
   */
  updatedDate?: Date | null;
  /**
   * Modifier group name.
   * @minLength 1
   * @maxLength 100
   */
  name?: string | null;
  /**
   * Group of item modifiers.
   * @maxSize 50
   */
  modifiers?: Modifier[];
  /** Modifier group details. */
  rule?: Rule;
  /** Extended fields. */
  extendedFields?: ExtendedFields;
  /**
   * IDs of the business locations ([SDK](https://dev.wix.com/docs/sdk/backend-modules/restaurants/wix-restaurants-new/about-business-locations) | [REST](https://dev.wix.com/docs/rest/business-solutions/restaurants/wix-restaurants-new/about-business-locations)) where these item modifier groups are available.
   * @maxSize 100
   * @format GUID
   * @readonly
   */
  businessLocationIds?: string[];
}

export interface Modifier {
  /**
   * Item modifier ID.
   * @format GUID
   */
  id?: string;
  /**
   * Whether the item modifier is pre-selected.
   * Default: `false`.
   */
  preSelected?: boolean | null;
  /** Item modifier price details. */
  additionalChargeInfo?: AdditionalChargeInfo;
}

export interface AdditionalChargeInfo {
  /**
   * Additional charge for the item modifier. A value of `0` means the item modifier is free.
   * @decimalValue options { gte:0.00, maxScale:3 }
   */
  additionalCharge?: string | null;
}

export interface Rule {
  /** Whether the items from the modifier group must be selected. */
  required?: boolean | null;
  /**
   * Minimum number of item modifiers a site visitor must select. The value must be lower or equal to the available item modifiers in the group.
   * Default: `0`.
   * @max 10
   */
  minSelections?: number | null;
  /**
   * Maximum number of item modifiers a site visitor may select. Must be greater than or equal to the value of `minSelections`.
   * @max 12
   */
  maxSelections?: number | null;
}

export interface ExtendedFields {
  /**
   * Extended field data. Each key corresponds to the namespace of the app that created the extended fields.
   * The value of each key is structured according to the schema defined when the extended fields were configured.
   *
   * You can only access fields for which you have the appropriate permissions.
   *
   * Learn more about [extended fields](https://dev.wix.com/docs/rest/articles/getting-started/extended-fields).
   */
  namespaces?: Record<string, Record<string, any>>;
}

export interface ExternalReferenceInfo {
  /**
   * ID of the external integrator app\system associated with this object.
   * @format GUID
   */
  externalAppId?: string | null;
  /**
   * Reference to an external resource ID. Used to link this object to a specific entity in an external system.
   * @maxLength 100
   */
  externalEntityId?: string | null;
  /** Last time this modifier group was synced with the external app. */
  lastSyncDate?: Date | null;
  /** Whether the connection to the external system is currently active. */
  connected?: boolean | null;
}

